:root{
  --tertiary: #ff0043;
}

.navbar {
    /* background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
     */
     background: #090808;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    overflow:hidden;
    z-index: 5;
  }
  
  .navbar-logo {
    color: #fff;
    justify-self: start;
    margin:auto;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    padding-left: 2vw;
  }
  
  .nav-menu {
    display: grid;
    grid-template-columns: repeat(6, auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 80vw;
    justify-content: end;
    margin-right: 2rem;
    overflow: hidden;
  }

  
  .nav-item {
    display: flex;
    align-items: center;
    height: 17vh;
  }
  
  .nav-links {
    color: white;
    text-decoration: none;
    padding: 0.5rem 1rem;
    
  }
  
  .nav-links:hover {
    background-color: var(--tertiary);
    transition: all 0.2s ease-out;
  }
  
  .fa-bars {
    color: #fff;
  }
  
  .nav-links-mobile {
    display: none;
  }
  
  .menu-icon {
    display: none;
  }

  .logo-rooney {
    width: 5vw;
    padding-left: 2vw;
  }
  
  @media screen and (max-width: 1200px) {

    *{
      overflow-x: hidden;
    }
    .fa-caret-down {
      display: none;
    }
    .NavbarItems {
      position: relative;
    }
  
    .nav-menu {
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 90vh;
      position: absolute;
      top: 80px;
      left: 100%;
      opacity: 1;
      transition: all 0.5s ease;
      
    }
  
    .nav-menu.active {
      background: #242222;
      left: 0;
      opacity: 1;
      transition: all 0.5s ease;
      z-index: 200;
    }
  
    .nav-links {
      text-align: center;
      padding: 2rem;
      width: 100%;
      display: table;
    }
  
    .nav-links:hover {
      background-color: var(--tertiary);
      border-radius: 0;
    }
  
    .navbar-logo {
      position: absolute;
      top: 0;
      left: 0;
      transform: translate(25%, 50%);
    }
  
    .menu-icon {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      transform: translate(-100%, 60%);
      font-size: 1.8rem;
      cursor: pointer;
    }
  
    .fa-times {
      color: #fff;
      font-size: 2rem;
    }
  
    .nav-links-mobile {
      display: block;
      text-align: center;
      padding: 1.5rem;
      margin: 2rem auto;
      border-radius: 4px;
      width: 80%;
      background: var(--tertiary);
      text-decoration: none;
      color: #fff;
      font-size: 1.5rem;
    }
  
    .nav-links-mobile:hover {
      background: #fff;
      color: var(--tertiary);
      transition: 250ms;
    }
  
    button {
      display: none;
    }

    .logo-rooney{
      width: 6vw;
    }
  }


  @media screen and (max-width: 680px) {
    .navbar-logo{
      font-size: 1.5rem;
      margin: auto;
      padding-top: 1vw;
    }
    .menu-icon{
      margin: auto;
      padding-top: 1vw;
      font-size: 1.5rem;
    }
  }

  @media screen and (max-width: 440px) {
    .navbar-logo{
      font-size: 1rem;
      margin: auto;
      padding-top: 3vw;
    }
    .logo-rooney{
      width: 40px;
      /* padding: 2vw; */
    }
    .menu-icon{
      margin: auto;
      padding-top: 3vw;
      font-size: 1rem;
    }
  }